export default {
	data() { 
		return {
			source: 0,
			// addressList: [
			// 	{
			// 		name: '刘晓晓',
			// 		mobile: '18666666666',
			// 		addressName: '贵族皇仕牛排(东城店)',
			// 		address: '北京市东城区',
			// 		area: 'B区',
			// 		default: true
			// 	},{
			// 		name: '刘大大',
			// 		mobile: '18667766666',
			// 		addressName: '龙回1区12号楼',
			// 		address: '山东省济南市历城区',
			// 		area: '西单元302',
			// 		default: false,
			// 	}
			// ],
			addressList:[]
		}
	},
	onLoad(option){
		console.log(option.source);
		this.source = option.source;
	},
	onShow(){
		this.getList();
	},
	methods: {
		//选择地址
		checkAddress(item){
			if(this.source == 1){
				//this.$api.prePage()获取上一页实例，在App.vue定义
				this.$api.prePage().addressData = item;
				uni.navigateBack()
			}
		},
		addAddress(type, item){
			uni.navigateTo({
				url: `/pagesA/address/addressManage?type=${type}&data=${JSON.stringify(item)}`
			})
		},
		//添加或修改成功之后回调
		refreshList(type){
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			this.getList()
		},
		getList(){
			this.$getJson("/jsp/api/addr/addrList.jsp",{memberId:uni.getStorageSync(`userInfo`).id},"POST",res=>{
				if(res.success=='true'){
					this.addressList = res.data.addr;
					console.log(this.addressList)
				}
			});
		},
		delAddress(id){
			this.$getJson("/jsp/api/addr/doDel.jsp",{id:id},"POST",res=>{
				console.log('delAddress',res.data)
				if(res.success=='true'){
					//
					this.getList();
				}
			});
		},
	}
}
